@font-face {
  font-family: 'BLMelody-Bold';
  src: url('../fonts/BLMelody-Bold.otf') format('opentype'),
       url('../fonts/BLMelody-Bold.woff') format('woff'),
       url('../fonts/BLMelody-Bold.woff2') format('woff2');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'BLMelody-Light';
  src: url('../fonts/BLMelody-Light.otf') format('opentype'),
       url('../fonts/BLMelody-Light.woff') format('woff'),
       url('../fonts/BLMelody-Light.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}