@use '../../styles/mixins.scss' as mixins;

.section {
  margin-top: 23px;
  display: grid;
  overflow: hidden;

  @include mixins.onTablet {
    overflow: unset;
  }


  padding: 0 0 35px 30px;

  @media (min-width: 768px) {
    padding: 0 50px;
  }
  @media (min-width: 1024px) {
    padding: 0 60px;
  }
  @media (min-width: 1550px) {
    padding: 0 80px;
  }
  @media (min-width: 1920px) {
    padding: 0 100px;
  }
}

.themeWrapper {
  display: none;
  transform: rotate(90deg);
  width: 40px;
  height: 40px;

  @include mixins.onDesktop {
    display: grid;
    align-self: flex-end;
  }

  @include mixins.on1920 {
   align-self: center;
  }
}

// <--- Main Photo and OUT LINE text --->

.image_container {
  display: flex;
  align-items: center;
  justify-content: flex-end;

  @include mixins.onTablet {
    transform: translate(0, -20%);
    grid-row: 1/4;
  }

  @include mixins.onDesktop {
    // justify-content: unset;
  }
}

.image_wrapper {
  position: relative;
  display: inline-block;
}

.headName_wrapper {
  font-family: 'BLMelody-Bold';
  font-weight: 700;
  letter-spacing: 0.08em;
  font-size: 70px;
  line-height: 75px;

  @include mixins.onTablet {
    font-size: 100px;
    line-height: 108px;
  }

  @include mixins.onDesktop {
    font-size: 140px;
    line-height: 151px;
  }

  @include mixins.on1920 {
    font-size: 160px;
    line-height: 172px;
  }
}

.text_container {
  position: absolute;
  z-index: 2;
  transform: translate(-117px, 0);

  @include mixins.on1024 {
    transform: translate(-208px, 0);
  }

  @include mixins.onDesktop {
    transform: translate(-197px, 0);
  }
}

.slash {
  display: block;
  width: 0;
  height: 42px;
  background-color: #4b4343;
  border: 2px solid #4b4343;
  border-radius: 2px;
  margin-left: 17px;
  margin-bottom: 5px;
  transform: rotate(36deg);

  @include mixins.onDesktop {
    margin-bottom: 27px;
    height: 50px;
  }
}

.headName {
  background-image: linear-gradient(
    to right,
    #bbd1ff 49%,
    var(--theme-background) 48%
  );

  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;

}

.mainPicture {
  width: 210px;
  height: 349px;

  @include mixins.onTablet {
    width: 251px;
    height: 596px;
  }

  @include mixins.on1024 {
    width: 342px;
    height: 544px;
  }

  @include mixins.onDesktop {
    width: 385px;
    height: 832px;
  }

  @include mixins.on1920 {
    width: 465px;
    height: 1003px;
  }
}

// <--- Main Photo and OUT LINE text END --->

.spinTextWrapper {
  display: flex;

  justify-content: end;
  padding-right: 20%;
  transform: translate(0, -50%);

  // background-image: linear-gradient(
  //   to right,
  //   #bbd1ff 49%,
  //   var(--theme-background) 48%
  // );

  // background-clip: text;
  // -webkit-background-clip: text;
  // -webkit-text-fill-color: transparent;
  // -moz-background-clip: text;
  // -moz-text-fill-color: transparent;

  @include mixins.onTablet {
    grid-row: 1/2;
    transform: unset;
    justify-content: unset;
  }
}


// Categories

.categorie {
  &__wrapper {
    display: flex;
    gap: 38px;
    align-items: center;

    padding: 25px;
    position: relative;

    &:not(:last-child)::after {
      position: absolute;
      bottom: 0;
      content: '';
      background: var(--main-gradient);
      height: 1px;
      width: 100vw;

      transform: translate(-55px, 0);

      @include mixins.onTablet {
        transform: unset;
      }
    }

    @include mixins.onTablet {
      align-items: start;
      flex-direction: column;
      gap: 40px;
      grid-row: 4/5;

      padding: 0 0 0 42px;

      &:not(:last-child)::after {
        right: 0;
        bottom: unset;
        width: 1px;
        height: 100%;
      }
    }

    @include mixins.onDesktop {
      grid-row: 2/3;
    }
  }

  &__number {
    font-family: 'Roboto';
    font-weight: 300;
    font-size: 30px;
    line-height: 35px;

    background: var(--main-gradient);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  &__name {
    font-family: 'BLMelody-Light';
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 27px;
    overflow-wrap: break-word;
    max-width: 200px;

    & span {
      font-family: 'BLMelody-Bold';
    }
  }
}

.wrapper_02 {
  @include mixins.onTablet {
    margin-top: 100px;
  }
}

.wrapper_03 {
  @include mixins.onTablet {
    margin-top: 200px;
  }
}
